const BASE_URL = process.env.REACT_APP_API_BASE_URL || "http://8.133.207.19:5000";

// SSEè¿æ¥åˆ›å»ºå‡½æ•° - åŒ…å«é‡è¿æœºåˆ¶
const createEventSourceWithRetry = (url, handlers, maxRetries = 3) => {
  let retryCount = 0;
  let es;

  const connect = () => {
    es = new EventSource(url);
    
    es.onopen = () => {
      retryCount = 0;
    };

    es.onerror = (err) => {
      if (retryCount < maxRetries) {
        retryCount++;
        setTimeout(() => {
          es.close();
          connect();
        }, 1000 * retryCount); // é€’å¢é‡è¯•å»¶è¿Ÿ
      } else {
        handlers.onError?.('è¿æ¥å·²æ–­å¼€,è¯·åˆ·æ–°é¡µé¢é‡è¯•');
        es.close();
      }
    };

    return es;
  };

  return connect();
};

// å¸¦è¶…æ—¶çš„fetchå‡½æ•°
const fetchWithTimeout = async (url, options = {}, timeout = 30000) => {
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), timeout);
  
  try {
    const response = await fetch(url, {
      ...options,
      signal: controller.signal
    });
    clearTimeout(timeoutId);
    
    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || 'è¯·æ±‚å¤±è´¥');
    }
    
    return response;
  } catch (error) {
    if (error.name === 'AbortError') {
      throw new Error('è¯·æ±‚è¶…æ—¶ï¼Œè¯·é‡è¯•');
    }
    throw error;
  }
};

// åˆ›å»ºå…«å­—åˆ†æSSEæµ
export const createOrderStream = (eightCharacters, handlers) => {
  const url = `${BASE_URL}/api/orders/stream?eightCharacters=${encodeURIComponent(eightCharacters)}`;
  const es = createEventSourceWithRetry(url, handlers);

  // ç›‘å¬ä¸åŒç±»å‹çš„äº‹ä»¶
  es.addEventListener('stream', (e) => {
    try {
      const data = JSON.parse(e.data);
      handlers.onStream?.(data.content);
    } catch (error) {
      handlers.onError?.('æ•°æ®è§£æé”™è¯¯');
      es.close();
    }
  });

  es.addEventListener('stream_end', (e) => {
    try {
      const data = JSON.parse(e.data);
      handlers.onComplete?.(data.orderId);
      es.close();
    } catch (error) {
      handlers.onError?.('æ•°æ®è§£æé”™è¯¯');
      es.close();
    }
  });

  es.addEventListener('error', (e) => {
    try {
      if(e.data) {
        const data = JSON.parse(e.data);
        handlers.onError?.(data.message);
      }
      es.close();
    } catch (error) {
      handlers.onError?.('è¿æ¥é”™è¯¯');
      es.close();
    }
  });

  return es;
};

// æ”¯ä»˜å’Œè¯¦ç»†åˆ†æSSEæµ
export const initiatePayment = (prevOrderId, analysisType, handlers) => {
  const url = `${BASE_URL}/api/orders/${prevOrderId}/payment?type=${analysisType}`;
  const es = createEventSourceWithRetry(url, handlers);

  const safeParseJson = (data, defaultValue = null) => {
    try {
      return JSON.parse(data);
    } catch (error) {
      handlers.onError?.('æ•°æ®è§£æé”™è¯¯');
      es.close();
      return defaultValue;
    }
  };

  // ç›‘å¬æ”¯ä»˜ç›¸å…³äº‹ä»¶
  es.addEventListener('payment', (e) => {
    const data = safeParseJson(e.data);
    if (data?.qrcodeUrl && data?.orderId) {
      handlers.onPaymentQR?.(data.qrcodeUrl, data.orderId);
    }
  });

  es.addEventListener('payment_success', () => {
    handlers.onPaymentstatus?.();
  });

  // ç›‘å¬åˆ†æå†…å®¹æµå¼ä¼ è¾“
  es.addEventListener('stream', (e) => {
    const data = safeParseJson(e.data);
    if (data?.content) {
      handlers.onAnalysisChunk?.(data.content);
    }
  });

  es.addEventListener('stream_end', () => {
    handlers.onAnalysisEnd?.();
    es.close();
  });

  es.addEventListener('error', (e) => {
    if (e.data) {
      const data = safeParseJson(e.data);
      if (data?.message) {
        handlers.onError?.(data.message);
      } else {
        handlers.onError?.('æ”¯ä»˜æµç¨‹å‡ºé”™');
      }
    }
    es.close();
  });

  return es;
};

// è·å–åˆ†æå†…å®¹
export const fetchAnalysisContent = async (prevOrderId, analysisType, analysisOrderId) => {
  const url = `${BASE_URL}/api/orders/${prevOrderId}/analysis?type=${analysisType}&analysisOrderId=${analysisOrderId}`;
  
  try {
    const response = await fetchWithTimeout(url);
    const data = await response.json();
    if (!data?.content) {
      throw new Error('è¿”å›æ•°æ®æ ¼å¼é”™è¯¯');
    }
    return data.content;
  } catch (error) {
    throw new Error('è·å–åˆ†æå†…å®¹å¤±è´¥: ' + error.message);
  }
};