import React, { useEffect, useState } from 'react';
import { QRCodeSVG } from 'qrcode.react'; 
import PropTypes from 'prop-types';

const PaymentModal = ({ isOpen, qrcodeUrl, onClose }) => {
  const [countdown, setCountdown] = useState(600); // é»˜è®¤10åˆ†é’Ÿ
  // å€’è®¡æ—¶æ•ˆæœ
  useEffect(() => {
    if (!isOpen) return;

    const timer = setInterval(() => {
      setCountdown(prev => Math.max(0, prev - 1));
    }, 1000);

    return () => clearInterval(timer);
  }, [isOpen]);

  // æ ¼å¼åŒ–æ—¶é—´æ˜¾ç¤º
  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (!isOpen) return null;

  return (
    <div className="payment-modal">
      <div className="modal-content">
        <div className="modal-header">
          <h3>æ‰«ç æ”¯ä»˜ Â¥1.99</h3>
          <button className="close-button" onClick={onClose}>Ã—</button>
        </div>
        
        <div className="qrcode-wrapper">
          {qrcodeUrl ? (
            <>
              <QRCodeSVG 
                value={qrcodeUrl} 
                size={200}
                fgColor="#2d3748"
                level="H"
              />
              <p className="countdown">å‰©ä½™æ—¶é—´: {formatTime(countdown)}</p>
            </>
          ) : (
            <div className="loading">æ­£åœ¨ç”Ÿæˆæ”¯ä»˜ç ...</div>
          )}
        </div>

        <div className="modal-footer">
          <p>è¯·ä¿å­˜äºŒç»´ç ï¼Œä½¿ç”¨æ”¯ä»˜å®æ‰«æå®Œæˆæ”¯ä»˜</p>
        </div>
      </div>
    </div>
  );
};

PaymentModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  qrcodeUrl: PropTypes.string,
  onClose: PropTypes.func.isRequired
};

export default PaymentModal;
